.document {
  width: 240px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 20px;
}

.document__damper {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 50px;
  background-color: #D2E8BA;
  align-items: center;
  border-radius: 5px;
  z-index: 2;
}

.document__forvard-damper {
  margin-right: 30px;
}

.document__title {
  margin-left: 30px;
  width: 160px;
  height: 20px;
  // на случай очень длинного текста
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.document__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: -5px;
  height: 180px;
  background-color: beige;
  border-radius: 0 0 5px 5px;
}

.document__text {
  padding-top: 10px;
  margin-left: 5px;
  height: 56px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.document__info-close {
  display: none;
}

.document__date {
  margin-left: 7px;
}

.document__id {
  margin-left: 7px;
  margin-bottom: 10px;
}

.document__img {
  margin-left: auto;
  margin-right: auto;
  height: 50px;
}